.student-card {
  background: #1e1e1e;
  border: 2px solid #d32f2f; /* rojo */
  border-radius: 12px;
  padding: 1rem;
  margin: 1rem;
  color: #f5f5f5;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  display: flex;
  align-items: center;
  gap: 1rem;
  box-shadow: 0px 4px 12px rgba(0,0,0,0.6);
}

.student-card:hover {
  transform: scale(1.05);
  background: #2a2a2a;
  box-shadow: 0px 6px 16px rgba(211, 47, 47, 0.7);
}

.student-img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  object-fit: cover;
  border: 2px solid #d32f2f;
}

.student-info {
  flex: 1;
}
